"use client"

import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from "@/components/ui/table"
import { Badge } from "@/components/ui/badge"
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card"
import { Activity, ExternalLink } from "lucide-react"
import { useStore } from "@/store/useStore"

export function SettlementFeed() {
  const transactions = useStore((state) => state.transactions)

  return (
    <Card className="col-span-1 lg:col-span-4">
      <CardHeader>
        <CardTitle className="flex items-center gap-2 text-sm font-medium">
            <Activity className="h-4 w-4 text-primary" />
            Onchain Settlement Feed (Arc L1)
        </CardTitle>
      </CardHeader>
      <CardContent>
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Shipment ID</TableHead>
              <TableHead>Proof Type</TableHead>
              <TableHead>Status</TableHead>
              <TableHead className="text-right">Amount</TableHead>
              <TableHead className="text-right">Finality</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {transactions.map((tx) => (
              <TableRow key={tx.id} className={tx.status === "SETTLED" ? "animate-pulse-glow" : ""}>
                <TableCell className="font-medium">{tx.shipmentId}</TableCell>
                <TableCell>{tx.proof}</TableCell>
                <TableCell>
                  <Badge 
                    variant={tx.status === "SETTLED" ? "default" : "secondary"}
                    className={tx.status === "SETTLED" ? "bg-emerald-500 hover:bg-emerald-600" : ""}
                  >
                    {tx.status}
                  </Badge>
                </TableCell>
                <TableCell className="text-right font-mono tabular-nums">{tx.amount}</TableCell>
                <TableCell className="text-right text-xs text-muted-foreground">
                    {tx.status === "SETTLED" ? (
                        <span className="flex items-center justify-end gap-1 text-emerald-500 font-bold">
                            &lt; 800ms <ExternalLink className="h-3 w-3" />
                        </span>
                    ) : (
                        <span className="animate-pulse text-amber-500">Confirming...</span>
                    )}
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </CardContent>
    </Card>
  )
}
