import Link from "next/link"
import { LayoutDashboard, Truck, Settings, ShieldCheck, Activity } from "lucide-react"
import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"

interface SidebarProps extends React.HTMLAttributes<HTMLDivElement> {}

export function Sidebar({ className }: SidebarProps) {
  return (
    <div className={cn("pb-12 h-full bg-sidebar text-sidebar-foreground border-r border-border", className)}>
      <div className="space-y-4 py-4">
        <div className="px-3 py-2">
          <h2 className="mb-2 px-4 text-lg font-semibold tracking-tight flex items-center gap-2">
            <ShieldCheck className="h-6 w-6 text-primary" />
            AgenticSCM
          </h2>
          <div className="space-y-1">
            <Button variant="ghost" className="w-full justify-start hover:bg-white/10" asChild>
              <Link href="/">
                <LayoutDashboard className="mr-2 h-4 w-4" />
                Command Center
              </Link>
            </Button>
            <Button variant="ghost" className="w-full justify-start hover:bg-white/10" asChild>
              <Link href="/shipments">
                <Truck className="mr-2 h-4 w-4" />
                Shipments
              </Link>
            </Button>
            <Button variant="ghost" className="w-full justify-start hover:bg-white/10" asChild>
              <Link href="/activity">
                <Activity className="mr-2 h-4 w-4" />
                Live Feed
              </Link>
            </Button>
            <Button variant="ghost" className="w-full justify-start hover:bg-white/10" asChild>
              <Link href="/settings">
                <Settings className="mr-2 h-4 w-4" />
                Settings
              </Link>
            </Button>
          </div>
        </div>
        <div className="px-3 py-2">
            <h2 className="mb-2 px-4 text-lg font-semibold tracking-tight">
              System Status
            </h2>
            <div className="px-4 text-sm text-muted-foreground">
                <div className="flex justify-between py-1">
                    <span>Arc Mainnet</span>
                    <span className="text-primary font-bold">Online</span>
                </div>
                <div className="flex justify-between py-1">
                    <span>Gemini Agent</span>
                    <span className="text-primary font-bold">Active</span>
                </div>
                <div className="flex justify-between py-1">
                    <span>Circle Paymaster</span>
                    <span className="text-primary font-bold">Ready</span>
                </div>
            </div>
        </div>
      </div>
    </div>
  )
}
