"use client"

import { ScrollArea } from "@/components/ui/scroll-area"
import { Badge } from "@/components/ui/badge"
import { cn } from "@/lib/utils"
import { Bot, Zap } from "lucide-react"
import { useStore } from "@/store/useStore"

interface IntelligencePanelProps {
  className?: string
}

export function IntelligencePanel({ className }: IntelligencePanelProps) {
  const logs = useStore((state) => state.logs)

  return (
    <div className={cn("h-full flex flex-col bg-background border-l", className)}>
      <div className="p-4 border-b flex items-center justify-between">
        <h3 className="font-semibold flex items-center gap-2">
          <Bot className="h-5 w-5 text-accent" />
          Gemini Intelligence
        </h3>
        <Badge variant="outline" className="text-accent border-accent animate-pulse">
          Live
        </Badge>
      </div>
      <ScrollArea className="flex-1 p-4">
        <div className="space-y-4 text-sm">
          {logs.map((log, index) => (
             <div key={index} className={cn("bg-muted/50 p-3 rounded-lg border border-border", log.type === 'SUCCESS' && "border-l-4 border-l-emerald-500", log.type === 'INFO' && "border-l-4 border-l-accent")}>
                <div className="flex items-center gap-2 text-xs text-muted-foreground mb-1">
                  <Zap className="h-3 w-3 text-yellow-500" />
                  <span>{log.timestamp}</span>
                </div>
                <p className="font-mono text-xs">
                  {log.message}
                </p>
              </div>
          ))}
        </div>
      </ScrollArea>
    </div>
  )
}
