"use client"

import {
  Drawer,
  DrawerContent,
  DrawerTrigger,
} from "@/components/ui/drawer"
import { Button } from "@/components/ui/button"
import { Bot } from "lucide-react"
import { IntelligencePanel } from "./IntelligencePanel"

export function IntelligenceDrawer() {
  return (
    <Drawer>
      <DrawerTrigger asChild>
        <Button 
            className="fixed bottom-6 right-6 h-14 w-14 rounded-full shadow-lg bg-accent hover:bg-accent/90 animate-pulse z-50 lg:hidden"
            size="icon"
        >
          <Bot className="h-6 w-6 text-white" />
        </Button>
      </DrawerTrigger>
      <DrawerContent className="h-[85vh]">
         <IntelligencePanel className="h-full border-none" />
      </DrawerContent>
    </Drawer>
  )
}
