import { create } from 'zustand'

export interface Log {
    id: string
    timestamp: string
    message: string
    type: 'INFO' | 'SUCCESS' | 'WARNING' | 'ERROR'
}

export interface Transaction {
    id: string
    shipmentId: string
    amount: string
    status: 'PENDING' | 'PROCESSING' | 'SETTLED' | 'FAILED'
    proof: string
    time: string
}

interface StoreState {
    logs: Log[]
    transactions: Transaction[]
    addLog: (message: string, type?: 'INFO' | 'SUCCESS' | 'WARNING' | 'ERROR') => void
    addTransaction: (tx: Transaction) => void
    triggerDelivery: (shipmentId: string) => void
}

export const useStore = create<StoreState>((set, get) => ({
    logs: [
        { id: '1', timestamp: new Date().toLocaleTimeString(), message: 'System initialized. Listening for IoT events.', type: 'INFO' }
    ],
    transactions: [
        { id: 'TX-001', shipmentId: 'SHP-9928', amount: '2,450.00 USDC', status: 'SETTLED', proof: 'GPS_GEOFENCE', time: '10:45:32 AM' }
    ],
    addLog: (message, type = 'INFO') => set((state) => ({
        logs: [...state.logs, {
            id: Math.random().toString(36),
            timestamp: new Date().toLocaleTimeString(),
            message,
            type
        }]
    })),
    addTransaction: (tx) => set((state) => ({
        transactions: [tx, ...state.transactions]
    })),
    triggerDelivery: (shipmentId) => {
        const { addLog } = get()
        
        addLog(`IoT Signal Received: Shipment ${shipmentId} Arrived`, 'INFO')
        
        setTimeout(() => {
            addLog(`Verifying GPS coordinates for ${shipmentId}...`, 'INFO')
        }, 500)

        setTimeout(() => {
             addLog(`Gemini Analysis: 99.8% confidence. Payment Approved.`, 'SUCCESS')
             set((state) => ({
                transactions: [{
                     id: `TX-${Math.floor(Math.random() * 1000)}`,
                     shipmentId,
                     amount: '1,200.00 USDC',
                     status: 'PROCESSING',
                     proof: 'IOT_SENSOR',
                     time: new Date().toLocaleTimeString()
                 }, ...state.transactions]
             }))
        }, 1500)

        setTimeout(() => {
            set((state) => ({
                transactions: state.transactions.map(t => 
                    t.shipmentId === shipmentId && t.status === 'PROCESSING' 
                    ? { ...t, status: 'SETTLED' as const } 
                    : t
                )
            }))
             addLog(`Payment Settled on Arc L1: <800ms finality`, 'SUCCESS')
        }, 2500)
    }
}))
